/* Reset default styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* Main container styles */
.main-container {
  width: 100%;
  height: 100vh;
  padding: 0.5rem;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  overflow: hidden; /* Prevent scrolling */
}

/* Content container */
.content-container {
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
  padding: 0.5rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0.5rem; /* Reduce gap */
}

/* Title styles */
h1 {
  text-align: center;
  width: 100%;
  margin-bottom: 0.5rem;
  font-size: 1.25rem; /* Slightly smaller */
}

/* Button group styles */
.button-group {
  display: flex;
  width: 100%;
  gap: 0.5rem;
  margin: 0.25rem 0;
  align-items: baseline; /* This is the key change */
}

/* Button base styles */
.button {
  all: unset;
  box-sizing: border-box;
  flex: 1;
  height: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1rem;
  border-radius: 0.25rem;
}

/* Button colors */
.button-blue {
  background-color: #3b82f6;
  color: white;
}

.button-red {
  background-color: #ef4444;
  color: white;
}

.button-green {
  background-color: #22c55e;
  color: white;
}

.button:disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

/* Slider container styles */
.slider-container {
  width: 100%;
  margin: 0.25rem 0;
}

.slider-container input[type="range"] {
  width: 100%;
  height: 44px; /* Better touch target */
}

/* Input group styles */
.input-group {
  display: flex;
  width: 100%;
  gap: 0.5rem;
  margin: 0.25rem 0;
  align-items: baseline;
}

/* Number input styles */
.number-input {
  flex: 1;
  height: 40px;
  padding: 0 0.75rem;
  border: 1px solid #ccc;
  border-radius: 0.25rem;
  font-size: 1rem;
  box-sizing: border-box;
  max-width: 120px;
}

/* Button in input group */
.input-group .button {
  flex: 2;
  min-width: unset;
}

/* Error message container styles */
.error-container {
  margin-top: 1rem;
}

/* Error message styles */
.error {
  color: #ef4444;
  font-size: 0.875rem;
}

/* Clock styles */
.clock-container {
  width: 100%;
  max-width: 400px;
  margin: 0.5rem auto 0;
  padding: 0 0.5rem;
}

.time-display {
  width: 100%;
  padding: 0.75rem;
  border-radius: 0.5rem;
  background-color: #f3f4f6;
}

/* Not connected message */
.time-display > span {
  width: 100%;
  padding: 0.75rem;
  text-align: center;
  background-color: #f3f4f6;
  border-radius: 0.5rem;
  color: #6b7280;
  min-height: 44px;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Sync button */
.button-blue {
  width: 100%;
  margin-top: 0.5rem;
}

.time-info {
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.time-label {
  display: flex;
  justify-content: space-between;
  gap: 1rem;
  font-size: 0.875rem;
}

.time-value {
  font-weight: bold;
}

/* Mobile optimizations */
@media (max-width: 480px) {
  .main-container {
    padding: 0.5rem;
  }

  .content-container {
    padding: 0.5rem;
  }

  h1 {
    font-size: 1.25rem;
  }

  .button {
    font-size: 0.875rem;
  }

  .number-input {
    font-size: 0.875rem;
  }

  .time-label {
    font-size: 0.75rem;
  }
}

@media (max-height: 600px) {
  .button {
    padding: 0 0.5rem;
    font-size: 0.875rem;
  }

  .number-input {
    padding: 0.5rem;
  }

  .time-display {
    padding: 0.5rem;
  }
}
